import { Link } from "react-router-dom";
import "../css/main.css";
import "../css/mainnav.css";
import { useSelector } from "react-redux";
import useCustomLogin from "../component/hooks/useCustomLogin";

const MainNav = () => {
  const loginState = useSelector(state => state.loginSlice)
  const { doLogout, isLogin } = useCustomLogin()
  return (
    <nav>
      <div id="nav_bar">
        <div className="menu_btn">
          <ul>
            <li className="depth1">
              <Link to={"/"}></Link>
            </li>

            <li className="depth1">
              <a href="/free"></a>
              <ul className="submenu">
                <li><Link to={"/notice"}></Link></li>
                <li><a href="/free"></a></li>
                <li><a href="/review"></a></li>
                <li><a href="/question"></a></li>
              </ul>
            </li>
            <li className="depth1">
              <Link to={"/map"}></Link>
            </li>
            <li className="depth1">
              <Link to={"/reservationlist"}></Link>
            </li>
          </ul>
        </div>
        <div className="user">
          <ul className="submenu">
            <div className="flex text-white">
              <Link to="/member/mypage" className="px-4"></Link>
              {!isLogin ?
                <>
                  <Link to="/register" className="px-4"></Link>
                  <Link to="/member/login"></Link>
                </>
                :
                <button onClick={doLogout}></button>
              }
            </div>
          </ul>
        </div>
      </div>
    </nav>
  )
};

export default MainNav